<template>
  <div class="container">
    <loading-component :value="loading" />
    <v-snackbar v-model="snackbar" :timeout="timeout">
      {{ message }}
    </v-snackbar>
    <v-form ref="form" lazy-validation>
      <v-text-field
        v-model="survey.title"
        label="Title"
        :rules="fieldRules"
        required
      ></v-text-field>
      <div v-for="(question, index) in updatedQuestions" :key="index">
        <div >
          <h3>Q{{ index + 1}} :</h3>
          <v-text-field
            v-if="question.parent_answer == 1"
            :label="'the if yes question for Q' + question.parent_index"
            disabled
            :rules="fieldRules"
            required
          ></v-text-field>

          <v-text-field
            v-if="question.parent_answer == 0"
            :label="'the if no question for Q' + question.parent_index"
            disabled
            :rules="fieldRules"
            required
          ></v-text-field>
          <v-text-field
            v-model="question.content"
            label="Content"
            :rules="fieldRules"
            required
          ></v-text-field>
          <v-btn @click="initializeQuestion(index, true)"
            >Add "if yes" question</v-btn
          >
          <v-btn @click="initializeQuestion(index, false)"
            >Add "if no" question</v-btn
          >
        </div>
      </div>

      <v-btn color="success" @click="save"> Save </v-btn>
    </v-form>
  </div>
</template>

<script>
import LoadingComponent from "@/components/LoadingComponent";

export default {
  name: "CreateSurvey",
  components: { LoadingComponent },
  computed: {
    updatedQuestions() {
      return this.survey.questions;
    },
  },
  data() {
    return {
      loading: false,
      title: "Create a Survey:",
      fieldRules: [
        (v) => !!v || "Field is required",
        (v) => (v && v.length >= 2) || "Field must be grater than 2 characters",
      ],
      timeout: 2000,
      snackbar: false,
      message: "",
      survey: {
        id: null,
        title: null,
        questions: [
          {
            content: null,
          },
        ],
      },
    };
  },

  methods: {
    save() {
      this.loading = true;
      this.axios
        .post("survey", this.survey)
        .then(() => {
          this.loading = false;
          this.message = "saved";
          this.snackbar = true;
          this.snackbarColor = "red";

        })
        .catch((e) => {
          this.message = e.data.message;
          this.snackbar = true;
          this.snackbarColor = "red";
      this.loading = false;

        });
    },
    initializeQuestion(index, answer) {
      if (
        this.survey.questions.filter(
          (question) =>
            question.parent_answer == answer && question.parent_index == index
        ).length
      )
        return;
      this.survey.questions.push({
        content: null,
        parent_answer: answer,
        parent_index: index,
      });
      console.log(this.survey.questions);
    },
  },
};
</script>

<style lang="css" scoped>
</style>
